@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;800&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Montserrat', Menlo, Monaco, Consolas, 'Courier New', sans-serif;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  width: 100%;
}

main,
section,
article,
aside,
figure,
figcaption,
header,
footer,
nav {
  display: block;
}

h1 {
  margin: 0 0 0;
}

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
  text-decoration: none;
  outline:none;
  color: white;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  line-height: 1.15;
  margin: 0;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button {
  border: none;
  background-color: transparent;
  font-family: inherit;
  padding: 0;
  cursor: pointer;
  outline: none;
}
legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

ul{
  list-style: none;
  padding:0;
  margin:0;
}

[type="checkbox"],
[type="radio"] {
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
}

[hidden] {
  display: none;
}
h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}
.map {
    height: calc(100vh - 55px);
}
.map-holder {
    position: relative;
}
.marker {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 18px;
    height: 18px;
    background-color: #000;
    border: 2px solid #fff;
    border-radius: 100%;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    transform: translate(-50%, -50%);
}

.active {
    border-radius: 50%;
    box-shadow: 0 14px 28px rgba(0,0,0,0.8), 0 10px 10px rgba(0,0,0,0.2);
}

.inactive {
    border: none;
}
* Slider */
.slick-slider
{
    position: relative;

    display: block;
    box-sizing: border-box;

    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;

    -webkit-touch-callout: none;
    -khtml-user-select: none;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}
.slick-list
{
    position: relative;

    display: block;
    overflow: hidden;

    margin: 0;
    padding: 0;
}
.slick-list:focus
{
    outline: none;
}
.slick-list.dragging
{
    cursor: pointer;
    cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list
{
    transform: translate3d(0, 0, 0);
}
.slick-track
{
    position: relative;
    top: 0;
    left: 0;

    display: block;
    margin-left: auto;
    margin-right: auto;
}
.slick-track:before,
.slick-track:after
{
    display: table;

    content: '';
}
.slick-track:after
{
    clear: both;
}
.slick-loading .slick-track
{
    visibility: hidden;
}
.slick-slide
{
    display: none;
    float: left;

    height: 100%;
    min-height: 1px;
}
[dir='rtl'] .slick-slide
{
    float: right;
}
.slick-slide img
{
    display: block;
}
.slick-slide.slick-loading img
{
    display: none;
}
.slick-slide.dragging img
{
    pointer-events: none;
}
.slick-initialized .slick-slide
{
    display: block;
}
.slick-loading .slick-slide
{
    visibility: hidden;
}
.slick-vertical .slick-slide
{
    display: block;

    height: auto;

    border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
    display: none;
}
.slick-current .hotel_cards-item{
    box-shadow: 0 14px 28px rgba(0,0,0,0.8), 0 10px 10px rgba(0,0,0,0.2);
}
.hotel_cards {
    position: absolute;
    width: 100%;
    bottom: 10px;
}
.hotel_cards-item {
    width: 280px;
    font-size: 14px;
    font-weight: 300;
    padding: 15px;
    background-color: #fff;
    min-height: 226px;
    position: relative;
}
.hotel_card-details {
    display: flex;
    margin-bottom: 15px;
}
.hotel_card-details--info {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.hotel_card-details--photo {
    width: 40%;
    margin-right: 15px;
    min-height: 140px;
}
.hotel_card-details--photo img {
    max-width: 100px;
    width: 100%;
    height: auto;
}
.info_title {
    font-weight: 700;
    text-align: left;
    font-size: 13px;
}
.info_distance {
    text-transform: uppercase;
    font-weight: 500;
    font-size: 11px;
}
.info_price {
    font-size: 20px;
    font-weight: 700;
}
.info_desc {
    font-size: 11px;
    text-overflow: ellipsis;
    width: 100%;
    overflow: hidden;
}
.card_button {
    width: 100%;
    height: 40px;
    background-color: #F7941D;
    color: #fff;
    font-weight: 700;
}


.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 600px;
    background-color: white;
    overflow: scroll;
    height: 100vh;
}
.form-modal{
    padding: 20px!important;
}
.form-modal label{
    width: 100% !important;
}
.form-modal input{
    width: 100% !important;
    margin-bottom: 20px;
    height: 36px;
}
.slick-list{
    overflow-y: visible;
}
.btn{
    padding: 10px;
    border-radius: 3px;
}
.submit-form-btn{
    background-color: cornflowerblue;
    color: white;
    margin-right: 3px;
}
.close-form-btn{
    background-color: darkgray;
    color: white;
    margin-right: 3px;
}
.header {
    height:55px;
    background-color: #EBEAE6;
    z-index: 1;
}
.header_nav {
    padding: 0 17px;
    width: 100%;
    height: 100%;
}
.header_menu {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.fixed-header {
    position: fixed;
    top: 0;
    width: 100%
}
